import { Component, OnInit } from "@angular/core";

import { Http } from "@angular/http";
import { Router } from "@angular/router";
import { BASEURL } from "app/showcase/custom-components/config";
import { API } from "app/custom-components/services/api";
@Component({
    templateUrl: "./security-login.component.html",
    styleUrls: ["./security-login.component.scss"]
})
export class SecurityLoginComponent implements OnInit {


    loginUser = { source: "boss", mobile: '', password: '' };
    errorFlag: boolean = false;

    errorInfo: string = '';

    logining: boolean = false;
    login: boolean = false;
    isLoggedIn: boolean = false;
    loginText: string = "登录";

    msgs: any[] = [];

    copyrightYear: number = new Date().getFullYear();

    phoneReg: RegExp = /^(((13[0-9]{1})|(12[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/;



    constructor(public http: Http, public api: API, public router: Router) {
    }

    ngOnInit(): void {
    }

    inputFocus($event): void {
        this.errorFlag = false;
    }

    doLogin() {
        this.loginText = "登录中...";
        this.login = true;
        this.errorFlag = false;
        if (!this.valid()) {
            setTimeout(() => {
                this.login = false;
                this.loginText = "登录";
            }, 100);
            return;
        };
        if (this.logining) {
            return;
        };//正在登录中
        this.logining = true;
        this.http.post(BASEURL + "/login", this.loginUser).map(res => {
            return res.json();
        }).subscribe(json => {
            if (json && json.jwt) {
                this.show();
                localStorage.setItem("jwt", json.jwt);
                setTimeout(() => {
                    this.getUserInfo();
                    this.login = false;
                    this.loginText = "登录";
                    // this.router.navigate(['/modules'])
                }, 300);
            } else {
                this.error();
            }
            this.logining = false;
        }, error => {
            this.login = false;
            this.loginText = "登录";
            this.errorFlag = true;
            if (error && error['_body']) {
                try {
                    let body = JSON.parse(error._body);
                    if (body['code'] === -1) {
                        this.errorInfo = body['error'];
                    } else {
                        this.errorInfo = '登录失败，请检查账号和密码';
                    }
                } catch (e) {
                    this.errorInfo = '服务器出错';
                }

            } else {
                this.errorInfo = '登录失败，请检查账号和密码';
            }
            this.logining = false;
        });
    }

    /**
     * 登录验证
     * @returns {boolean}
     */
    valid(): boolean {
        if (!this.loginUser.mobile) {
            this.errorFlag = true;
            this.errorInfo = '用户名不能为空';
            return false;
        } else if (!this.phoneReg.test(this.loginUser.mobile)) {
            this.errorFlag = true;
            this.errorInfo = '请输入正确的手机号码';
            return;
        } else if (!this.loginUser.password) {
            this.errorFlag = true;
            this.errorInfo = '密码不能为空';
            return false;
        }
        return true;
    }

    /**
     * 获取用户信息
     */
    getUserInfo() {
        this.checkUserLogin().then(isLogin => {
            if (isLogin) {
                this.router.navigateByUrl("/");
            } else {
                localStorage.removeItem("jwt");
            }
        }).catch(err => {
            this.errorFlag = true;
            this.errorInfo = '获取用户信息失败';
        })
    }

    /**
    * 获取用户信息
    * @returns {boolean}
    */
    checkUserLogin(): any {
        try {
            return new Promise((resolve, reject) => {
                this.api.call("SecurityController.currentUser").ok(json => {
                    if (json.result) {
                        localStorage.setItem('currentUser', JSON.stringify(json.result.attributes));
                        // localStorage.setItem('permissions', JSON.stringify(json.result.permissions));
                        this.isLoggedIn = true;
                        resolve(this.isLoggedIn)
                    } else {
                        this.isLoggedIn = false;
                        resolve(this.isLoggedIn)
                    }
                }).fail((err) => {
                    localStorage.clear();
                    this.isLoggedIn = false;
                    this.router.navigate(['/security/login']);
                    reject(err);
                });
            });
        } catch (e) {
            localStorage.clear();
            this.isLoggedIn = false;
        }
    }

    show(msg?: string) {
        msg = msg ? msg : '登录成功！';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: msg });
    }

    error(msg?: string) {
        msg = msg ? msg : '登录失败！';
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: msg });
    }

    warning(msg?: string) {
        msg = msg ? msg : '登录失败！';
        this.msgs = [];
        this.msgs.push({ severity: 'warn', summary: '提示', detail: msg });
    }

}



